class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeView: "View1",
      activePanel: "Main",

      scheme: 'bright_light',
      schemeDark: ['space_gray', 'client_dark'],

      fetchedUser: {
        id: 0,
        first_name: "Пользователь",
        last_name: "",
        sex: 0,
        photo_200: "https://vk.com/images/camera_100.png",
      },
      history: ['main'],

      popout: <ScreenSpinner size='large' />,
    }
  }

  go = (e) => {
    this.setState({ activePanel: e.currentTarget.dataset.to })
  };
  goBack = () => {
    const history = [...this.state.history];
    history.pop();
    const activePanel = history[history.length - 1];
    if (activePanel === 'main') {
      connect.send('VKWebAppDisableSwipeBack');
    }
    this.setState({ history, activePanel });
  }
  goForward = (activePanel) => {
    const history = [...this.state.history];
    history.push(activePanel);
    if (this.state.activePanel === 'main') {
      connect.send('VKWebAppEnableSwipeBack');
    }
    this.setState({ history, activePanel });
  }

  GetSexName = (sex) => {
    switch (sex) {
      case 1: return "Женский";
      case 2: return "Мужской";
      default: return "null :o";
    }
  }

  componentDidMount() {
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppInitResult':
          console.log("VKWebAppInitResult");
          break;
        case 'VKWebAppGetUserInfoResult':
          this.setState({ fetchedUser: e.detail.data });
          this.setState({ popout: '' });

          break;
        case 'VKWebAppUpdateConfig':
          const isDark = this.state.schemeDark.includes(e.detail.data.scheme);
          this.setState({ scheme: isDark ? 'space_gray' : 'bright_light' });

          break;
        default:
          console.log(e.type);
          console.log(e);

      }
    });
    connect.send("VKWebAppInit");
    connect.send('VKWebAppGetUserInfo', {});
  }


  render() {
    return (
      <ConfigProvider scheme={this.state.scheme} isWebView={true}>
        <Root activeView={this.state.activeView}>
          <View
            id="View1"
            popout={this.state.popout}
            activePanel={this.state.activePanel}
            onSwipeBack={this.goBack}
            history={this.state.history}
          >
            <Panel id="Main">
              <PanelHeader>byKIM</PanelHeader>

              <Group header={<Header mode="secondary">Добро пожаловать</Header>}>
                <RichCell
                  disabled
                  text={
                    <Subhead weight="regular" style={{ color: "var(--text_secondary)" }}>{
                      "id" + this.state.fetchedUser.id
                    }</Subhead>
                  }
                  before={
                    <Avatar size={72} src={this.state.fetchedUser.photo_200} />
                  }
                >
                  <Text weight="medium">{
                    this.state.fetchedUser.first_name + " " +
                    this.state.fetchedUser.last_name
                  }</Text>
                </RichCell>
              </Group>

              <Group header={<Header mode="secondary">Функции</Header>}>
                <SimpleCell onClick={this.go} data-to="FunctionDateReg" before={<Icon28CalendarOutline />}>Дата регистрации</SimpleCell>
              </Group>

            </Panel>

            <DateReg id="FunctionDateReg" go={this.go} user={this.state.fetchedUser} />


          </View>
        </Root>
      </ConfigProvider>
    )
  }
}

export default App;
